@import '../config/_colors';
@import '../config/_variables';
@import '../config/_breakpoints';

.fund {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  min-height: calc(100vh - #{$footer-height});
  padding-top: $nav-height; }

.fund__expiration {
  font-size: 28px;
  font-weight: 400;
  color: $color-black;
  margin-top: 8px; }

.fund__expiration--until {
  color: $color-grey;
  margin-right: 8px; }

.fund__expiration--unlocked {
  font-size: 36px;
  margin: 24px 0;
  margin-top: 8px; }

.fund__button {
  margin: 8px 0 0 0;
  width: 380px;
  max-width: 90vw;

  @include media($breakpoint-sm) {
    font-size: 24px; } }

.fund__details {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 16px; }

.fund__detail {
  font-size: 18px;
  color: $color-black;
  font-weight: 300;
  margin: 4px 0; }

.fund__detail--bold {
  font-weight: 400;
  margin-right: 4px; }

.fund-error {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  min-height: calc(100vh - #{$footer-height}); }

.fund-error__header {
  font-size: 36px;
  font-weight: 500;
  color: $color-brand;
  margin: 16px 0; }

.fund-error__info {
  font-size: 18px;
  font-weight: 300;
  color: $color-black; }

.fund-error__link {
  margin-left: 8px;
  color: $color-brand;
  text-decoration: none;
  font-weight: 400; }

.fund__warning {
  width: 380px;
  max-width: 90vw; }
