@import '../config/_colors';
@import '../config/_variables';

.button {
  font-size: 32px;
  height: 60px;
  width: 320px;
  margin: 0 8px;
  text-decoration: none;
  text-align: center;
  border-radius: $border-radius;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  font-family: Roboto;
  font-weight: 300; }

.button--outline {
  border: 1px solid $color-brand;
  background: linear-gradient($color-white, #fdfdfd);
  color: $color-brand; }

.button--solid {
  background: linear-gradient(to bottom, $color-brand , $color-brand-dark);
  color: $color-white;
  border: none; }
