@import '../config/_colors';
@import '../config/_variables';
@import '../config/_breakpoints';

.hero {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: calc(100vh - #{$footer-height});
  width: 100%; }

.hero__header {
  color: $color-black;
  font-size: 48px;
  font-weight: 500;
  margin-bottom: 0;
  text-align: center;
  max-width: 90vw;

  @include media($breakpoint-sm) {
    font-size: 40px; } }

.hero__subheader {
  color: $color-black;
  font-size: 24px;
  font-weight: 300;
  margin: 16px 0;
  text-align: center;
  max-width: 90vw;

  @include media($breakpoint-sm) {
    font-size: 18px; } }

.hero__buttons {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 16px;

  @include media($breakpoint-sm) {
    margin-top: 0; } }

.hero__button {
  max-width: 90vw;

  @include media($breakpoint-sm) {
    font-size: 24px; } }

.hero__learn-more {
  color: $color-brand;
  font-size: 24px;
  margin-top: 16px;
  text-decoration: none;
  font-weight: 300;

  @include media($breakpoint-sm) {
    font-size: 20px; } }
