@import '../config/_colors';
@import '../config/_variables';

.token-input {
  position: absolute;
  top: $label-height;
  list-style: none;
  padding: 0;
  margin: 0;
  width: 100%;
  z-index: 2;
  border-right: 1px solid rgba($color-grey, 0.5);
  border-left: 1px solid rgba($color-grey, 0.5);
  border-bottom-left-radius: $border-radius;
  border-bottom-right-radius: $border-radius; }

.token-input__dropdown {
  width: 100%;
  max-height: 278px;
  overflow-y: scroll;
  margin: 0;
  padding: 0; }

.token-input__search {
  height: 32px;
  width: 100%;
  border-radius: $border-radius;
  border: 1px solid $color-grey;
  text-align: center;
  font-size: 18px;
  font-weight: 400;
  color: $color-grey;
  font-family: Roboto;
  box-shadow: inset 0 -2px 2px rgba(0, 0, 0, 0.1); }

.token-input__logo {
  width: 36px;
  margin: 0 8px; }

.token-input__item {
  padding: 8px 0;
  background: white;
  cursor: pointer;
  display: flex;
  align-items: center; }

.token-input__item:hover {
  background: $color-light-grey; }

.token-input__symbol {
  font-size: 18px;
  font-weight: 400;
  color: $color-black;
  text-align: left;
  margin: 0; }

.token-input__name {
  font-size: 14px;
  font-weight: 300;
  color: $color-black;
  margin: 0; }
